import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/components/home/Home";
import SuggestMain from "@/components/suggest/SuggestMain";
import DocumentMain from "@/components/documentPage/DocumentMain";
Vue.use(VueRouter)

const routes = [
    {path: '',name:"Home",component: Home,meta: {index: 0}},
    {path: '/home',name:"stockEdit",component: Home,meta: {index: 0}},
    {path: '/suggest',name:"suggest",component: SuggestMain,meta: {index: 0}},
    {path: '/document',name:"document",component: DocumentMain,meta: {index: 0}},
]
/*实例化路由*/
const router = new VueRouter({
    routes:routes, // （简写）相当于 routes: routes
});

/*导出路由模块*/
export default router